            "寿喜锅、煲仔砂陶碗以及石锅多项功能于一身的多用型选手~简即繁，外形设计较日式，更加偏向于制作寿喜烧那种菜肴的铸铁锅多一点，再加上六层压铸技术，所以还没收到的时候以为会很重，结果超轻！包装精简，女生也能美美使用~拿到锅的第一件事情必然是开锅~开锅不繁琐，效益享万年～底部也用心地印上了胖选独有的logo~熠熠生辉~严选控一本满足！锅体受热很快，较普通锑锅相比受热更加均匀，几乎同等的水能优先烧开，媲美小巧的雪平锅，但容量规格又秒杀雪平锅~锅盖两个出气的设计很好地避免了凑上去观看内部蒸煮情况时被蒸汽误伤的情形，对了！贴心地设计了两个隔热小把手套，我宣布~这多功能锅，现在就是我宝贝里面第一心头好！！！",
          price: "¥149起",
        },
      ],
      timer: null,
      hots1: 0,
      indexflag: 0,
      home1: 0,
      show: "",
    };
  },
  methods: {
    // 侧边bar
    mover(e) {
      this.indexbar = e;
      this.show = e + 1;
    },
    mout(e) {
      this.show = "";
    },
    listchoose1(e) {
      this.listchoose = e;
      this.currindex = e - 1;
      this.indexflag = this.currindex;
    },
    // 轮播图向前切换
    prev() {
      if (this.currindex <= 0) {
        this.currindex = this.banner.length - 1;
      } else {
        this.currindex--;
      }
      this.indexflag = this.currindex;
      this.listchoose = this.currindex + 1;
    },
    // 轮播图向后切换
    next() {
      if (this.currindex >= this.banner.length - 1) {
        this.currindex = 0;
      } else {
        this.currindex++;
      }
      this.indexflag = this.currindex;
      this.listchoose = this.currindex + 1;
    },

    // 轮播图定时器
    time() {
      this.timer = setInterval(() => {
        this.next();
      }, 2000);
    },
    // 鼠标移入停止轮播
    stop() {
      clearInterval(this.timer);
    },
    // 鼠标移除开始轮播
    start() {
      clearInterval(this.timer);
      this.time();
    },
    // 品牌点击切换
    nextbrand() {
      this.hots1++;
      console.log(this.hots1);
      this.$refs.wid.style.left = -(this.hots.length / 2) * 250 + "px";
    },
    // 品牌点击切换
    back() {
      this.hots1--;
      console.log(this.hots1);
      this.$refs.wid.style.left = 0 + "px";
    },
    lza() {
      this.$store.commit("suibian", 8);
      this.$store.commit("suibian2", 8);
      this.$store.dispatch("sbaction", 10);
      this.$store.dispatch("sbaction2", 10);
    },
    // 鼠标移入出现
  },
  // 挂在完成启动轮播
  mounted() {
    this.time();
  },
};
</script>

